body {
	background: #01040B;
	font-size: 1.1rem;
	font-family: 'IBM Plex Sans', sans-serif;
}
.header-container {
	position: relative;
	background-color: black;
	width: 100%;
	height:90vh;
	overflow: hidden;
	padding: 6rem 0rem 6rem;
}
.header-container  video {
	position: absolute;
	top: 50%;
	left: 50%;
	min-width: 100%;
	min-height: 100%;
	width: auto;
	height: auto;
	z-index: 0;
	-ms-transform: translateX(-50%) translateY(-50%);
	-moz-transform: translateX(-50%) translateY(-50%);
	-webkit-transform: translateX(-50%) translateY(-50%);
	transform: translateX(-50%) translateY(-50%);
}
.header-container .container {
	position: relative;
	z-index: 2;
}
.header-container .overlay {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background-color: #01040B;
	opacity: 0.80;
	z-index: 1;
}
@media (pointer: coarse) and (hover: none) {
	.header-container {
		background: #01040B url('https://cdn.vnoc.com/realtydao/swirl-bg-min.png');
		background-size: cover;
	}
	.header-container video {
		display: none;
	}
}
.b-link {
	position: relative;
	margin-bottom: 2rem;
	display: inline-block;
}
.b-link:hover {
	text-decoration: none;
}
.header-title {
	font-family: 'IBM Plex Mono', monospace;
	font-weight: 800;
	font-size: 2.5rem;
	line-height: 1.15;
	margin-bottom: 15px;
	color: #fff;
}
.header-title span {
	color: #D3C54D;
}
.header-subtitle {
	font-family: 'IBM Plex Sans', sans-serif;
	font-size: 3rem;
	font-weight: 700;
	line-height: 1.5;
	margin-bottom: 0;
	color: #fbf3a7;
}
.learn-more-btn-section .btn {
	font-family: 'IBM Plex Sans', sans-serif;
}
.img-dom {
	width: 265px;
	animation: float 5s ease-in-out infinite;
}
.hs-color {
	color: #06C2AE !important;
}
.cus-li {
	background: rgba(1,3,10,.55);	
}
.list-group-item {
	background: none;
	border: solid 1px rgba(29,33,47,.35);
	padding: .55rem 1.25rem .65rem;
	border-radius: .55rem;
}
.cus-li .cri-desc {
	font-family: 'IBM Plex Sans', sans-serif;
	color: #06C2AE;
	font-weight: 700;
	font-size: 1.2rem;
}
.cus-li .cri-value {
	font-family: 'IBM Plex Sans', sans-serif;
	color: #D3C54D;
	font-weight: 700;
	font-size: 1.2rem;
}
.section-feat {
	background: #01040B;
	padding: 6rem 0rem 8rem;
}
.features-row {
	background: #01040B;
	margin-left: 0;
	margin-right: 0;
}
.feature-box .feature-icon {
	float: left;
	display: inline-block;
}
.feature-box .feature-icon img {
	width: 60px;
	vertical-align: initial;
}
.feature-box .feature-box-content {
	margin-left: 100px;
}
.feature-box .feature-box-content h3 {
	color: #fff;
	margin: 0 0 10px 0;
	line-height: normal;
	font-size: 1.4rem;
	font-family: 'IBM Plex Mono', monospace;
}
.feature-box .feature-box-content p {
	color: #fff;
	line-height: 23px;
	color: #ddd;
}
@media screen and (max-width: 991.98px) {
	.header-container {
		padding: 3rem 0rem 6rem;
		height: 100%;
		min-height: 60rem;
		width: 100%;
		overflow: hidden;
		padding-bottom: 2rem;
	}
}
@media screen and (max-width: 576px) {
	.header-container {		
		padding-bottom: 60rem;
	}
	.list-group-item {	    
		padding: .15rem 1.25rem .25rem;
	}
}
.section-footer {
	padding: 2rem 0rem 2rem;
	color: #06c2ae;
	font-size: 85%;
	font-family: 'IBM Plex Sans',sans-serif;
}
.section-footer a {
	color: #06c2ae;
}
.section-footer a:hover {
	color: #06c2ae;
	text-decoration: none;
}
